import React, { useEffect, useState } from 'react';
import { KakaoStyle } from '../../styles/Kakao';
import { InputArea, CheckStyle } from '../../styles/Form';
// import myData from '../../data.json'
import styled from 'styled-components';
import CkdLabel from './CkdLabel'

const { kakao } = window;


const Kakao = ((props) => {

    useEffect(() => {

        var mapContainer = document.getElementById('map'), //   div 
        mapOption = { 
            center: new kakao.maps.LatLng(33.450701, 126.570667), //  
            level: 3 //    
        }; 
        var map = new kakao.maps.Map(mapContainer, mapOption); //  

        // HTML5 geolocation     
        if (navigator.geolocation) {

            // GeoLocation    
            navigator.geolocation.getCurrentPosition(function(position) {
                
                var lat = position.coords.latitude, // 
                    lon = position.coords.longitude; // 
                
                var locPosition = new kakao.maps.LatLng(lat, lon), //    geolocation   
                    message = '<div style="padding:5px;"> ?!</div>'; //   
                
                //   
                displayMarker(locPosition, message);
                    
            });
            
        } else { // HTML5 GeoLocation         
            
            var locPosition = new kakao.maps.LatLng(33.450701, 126.570667),    
                message = '    .'
                
            displayMarker(locPosition, message);
        }

        //     
        function displayMarker(locPosition, message) {

            //  
            var marker = new kakao.maps.Marker({  
                map: map, 
                position: locPosition
            }); 
            
            var iwContent = message, //   
                iwRemoveable = true;

            //  
            var infowindow = new kakao.maps.InfoWindow({
                content : iwContent,
                removable : iwRemoveable
            });
            
            //    
            infowindow.open(map, marker);
            
            //    
            map.setCenter(locPosition);      
        }    
        
    },[])

    

    return (

        <KakaoStyle.mapArea>
            <KakaoStyle.mapHeader>
                <InputArea>
                    <CkdLabel label={''} id={'allView'} name={'chk01'}></CkdLabel>
                    <CkdLabel label={'01'} id={'coffee'} name={'chk02'}></CkdLabel>
                    <CkdLabel label={'02'} id={'food'} name={'chk03'}></CkdLabel>
                </InputArea>
            </KakaoStyle.mapHeader>
            <KakaoStyle.mapCont>
                <div id="map"></div>
            </KakaoStyle.mapCont>
        </KakaoStyle.mapArea>

        

    )
})

export default Kakao;